@media screen and (max-width: 750px) {
  .salesExtraWrap {
    font-size: 18px;
    display: flex;
    flex-direction: column;
    justify-items: space-evenly;
    align-items: center;
  }
  .ant-calendar-picker {
    width: 100% !important;
  }
  .ant-input {
    height: 40px;
  }
  .salesExtra {
    display: flex;
    margin-bottom: 10px;
    width: 100%;
    flex-wrap: wrap;
  }
  .salesExtra a {
    flex: 1 0 60px;
    margin-left: 20px;
    color: rgba(0, 0, 0, 0.65);
  }
  .salesExtra a:hover {
    color: #0091FF;
  }
  .salesExtra a.currentDate {
    color: #0091FF;
  }
}
@media screen and (min-width: 1200px) {
  .salesExtra {
    display: flex;
    justify-items: space-evenly;
    align-items: center;
  }
  .salesExtra {
    display: inline-block;
    margin-right: 24px;
  }
  .salesExtra a {
    margin-left: 24px;
    color: rgba(0, 0, 0, 0.65);
  }
  .salesExtra a:hover {
    color: #0091FF !important;
  }
  .salesExtra a.currentDate {
    color: #0091FF;
  }
}
.am-list-item .am-list-line .am-list-content {
  display: flex;
}
.calendar-list .right {
  float: right;
}
