input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset !important;
}
.dx-ui-input-addon {
  position: absolute;
  border: none;
  background: transparent;
  right: 10px;
  bottom: 1px;
  width: auto;
  line-height: 32px;
  font-size: 14px;
  z-index: 2;
  color: #000;
}
.dx-ui-input-addon + input {
  padding-right: 50px;
}
.dx-ui-input {
  position: relative;
}
.dx-ui-input textarea {
  resize: none;
}
